import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import {abi} from '../contracts/TokenTimelock.json'
import top100 from '../components/pancakeswap-extended.json';


class LockItem extends Component {

    static explorer;

    constructor(props) {
        super(props);

        this.state = {
          web3: props.web3,
          token: '',
          token_name: '',
          logo: 'https://tokens.pancakeswap.finance/images/0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c.png',
          loading: true
        };

        this.load_lock_data();
    
    }

    render() {
        if(this.state.loading){
            return <div class="card border-info mb-3" >
            <div class="spinner-border" role="status">
            </div>
            </div>;
        }

        return <div class="card border-info mb-3" >
            <div class="card-header">
                <a href={LockItem.explorer + this.state.token} target={'blank'} > Locked Asset {this.state.token_name} <img src={this.state.logo} width={25} height={25} /> </a> 
            </div>
            <div class="card-body">

            <h5 class="card-title">Details</h5>

            <ul class="list-group">
                <li class="list-group-item" >
                    Lock Contract: <b>{this.props.contract}</b><br/>
                    Send tokens to lock to this contract address!
                </li>
                <li class="list-group-item" >
                    Release Time: {this.state.releaseTime}
                </li>

                <li class="list-group-item" >
                    Beneficiary Wallet: {this.state.beneficiary}
                </li>
                <li class="list-group-item" >
                    <a class="btn btn-primary" onClick={() => {this.props.history.push('/lock-details/' + this.props.contract)}} role="button">Show Details</a>
                </li>
            </ul>
            </div>
            </div>;
    }

    async  load_lock_data() {

        let netid = await this.props.web3.eth.net.getId();
        LockItem.explorer = "https://bscscan.com/address/";
        if(netid == 97){
            LockItem.explorer = "https://testnet.bscscan.com/address/";
        }
    
        var myContract = new this.props.web3.eth.Contract(abi, this.props.contract);

        const accounts = await this.props.web3.eth.getAccounts();
        
        await myContract.methods.token().call({from: accounts[0]}, async function(error, result){
          //alert(result);

          let token_name;
          let logo;
          let token = result;

          [token_name, logo] = await this.find_name_by_address(result);
    
          this.setState({ token: token, logo: logo, token_name: token_name });
          
        }.bind(this));

        await myContract.methods.releaseTime().call({from: accounts[0]}, function(error, result){
            //alert(result);
            let releaseTime = new Date(result* 1e3).toDateString();
      
            this.setState({ releaseTime: releaseTime });
            
        }.bind(this));


        await myContract.methods.beneficiary().call({from: accounts[0]}, function(error, result){
            //alert(result);
            let beneficiary = result;
      
            this.setState({ beneficiary: beneficiary });
            
        }.bind(this));

        this.setState({ loading: false });
    
      }

      async  find_name_by_address(address) {

        let logo = 'https://tokens.pancakeswap.finance/images/0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c.png';
        let token_name = address;



        top100['tokens'].forEach(
            function (item){

                if(item['address'] == address){ 
                    token_name =  item['symbol'];
                    logo = item['logoURI'];
                }

        });

        return [token_name, logo];

      }

}


export default withRouter(LockItem);
