import React, { Component } from "react";
import { Formik, Form, Field, ErrorMessage } from 'formik';
import { withRouter } from 'react-router-dom';


import {dataService} from './user-context';
import {abi, bytecode} from '../contracts/TokenTimelock.json'
import {abi as o_abi, networks as o_networks} from '../contracts/OracleLocker.json'
import Select, {createFilter} from 'react-select'
import CreatableSelect from 'react-select/creatable';
import top100 from '../components/pancakeswap-extended.json';


declare var $ : any;

export const filterOption = (option, inputValue: string): boolean =>
  (option.label.toString().match(inputValue) || []).length > 0;


class LockTokenForm extends Component {

  static minABI = [
   //decimals
   {
     "constant":true,
     "inputs":[],
     "name":"decimals",
     "outputs":[{"name":"","type":"uint8"}],
     "type":"function"
   }
  ];

  static validateForm;

componentDidMount() {

  $( "#datepicker" ).datepicker();

  $('#datepicker').change(function(data) {
    // do stuff here;
    const value = $("#datepicker").datepicker("getDate").getTime() / 1000;
    this.setState({ datepicker: value });
  }.bind(this));
}

  constructor(props) {
    super(props);

    this.state = {
      datepicker: '',
      web3: props.web3,
      currentAccount: props.currentAccount,
      selectedOption: {value: '', label: '', icon: '', valid: true},
      options: this.load_tokens(),
      lockcontract: false,
      done: false
    };

    dataService.getData().subscribe(message => {
      this.state.web3 = message.value.web3;
      this.setState({ web3: message.value.web3, currentAccount: message.value.currentAccount });
    });

    this.load_tokens();

  }

  handleChange(e){

    let token = e.value;
    let errors = '';
    if (!token) {
      errors = 'Required';
    } else if (
      !/^0x[a-fA-F0-9]{40}$/.test(token)
    ) {
      errors = 'Invalid token address';
    }

    e["valid"] = errors == '';
    
    this.setState({selectedOption: e});

  }

  load_tokens(){

      let options = [];

      top100['tokens'].forEach(
          function (item){

            options.push({ 
              value: item['address'], 
              label: item['symbol'],
              icon: <img src={item['logoURI']} width={25} height={25} /> 
            })

      });

      return options;

  }

  render() {

    if(this.state.done){
      return (
        <div class="container">

            <div class="card border-info mb-3" >

                <div class="alert alert-success" role="alert">
                  <h5>Process completed!</h5>
                  <p>
                    Now you can send tokens to lock contact <b>{this.state.lockcontract}</b>
                  </p>
                  
                </div>

            </div>

        </div>
    );

    }

    if(this.state.lockcontract == 'loading'){
      return (
        <div class="container">

            <div class="card border-info mb-3" >

                <div class="alert alert-success" role="alert">
                    Waiting To create lock contract!
                    <div class="spinner-border" role="status">
                </div>
                </div>

            </div>

        </div>
    );

    }

    if(this.state.lockcontract.length > 12){
      return (
        <div class="container">

            <div class="card border-info mb-3" >

                <div class="alert alert-success" role="alert">
                  <h5>Lock contract created: {this.state.lockcontract}</h5>
                    Waiting to index your lock contract with current wallet {this.state.currentAccount}!
                    <div class="spinner-border" role="status">
                </div>
                </div>

            </div>

        </div>
    );

    }
    

    return (
      <div class="container">

          <div class="row d-flex justify-content-center">
              <div class="col-xl-7 col-lg-8 col-md-9 col-11 text-center">

                  <div class="card" >
                      <h5 class="text-center mb-4">Create a new lock</h5>


                      <Formik
                        initialValues={{
                          token: '',
                          price: 0,
                          datepicker: '',
                          beneficiary: ''
                        }}

                        validate={(values) =>{

                          const errors = {};

                          values.token = this.state.selectedOption.value;
                          console.log(values.token);

                          if (!values.token) {
                            errors.token = 'Required';
                          } else if (
                            !/^0x[a-fA-F0-9]{40}$/.test(values.token)
                          ) {
                            errors.token = 'Invalid token address';
                          }

                          if (!values.beneficiary) {
                            errors.beneficiary = 'Required';
                          } else if (
                            !/^0x[a-fA-F0-9]{40}$/.test(values.beneficiary)
                          ) {
                            errors.beneficiary = 'Invalid beneficiary address';
                          }

                          if (!values.price) {
                            errors.price = 'Invalid price value';
                          } else if (
                            isNaN(values.price) || values.price <= 0
                          ) {
                            errors.price = 'Invalid price value';
                          }

                          return errors;

                        }}
                        onSubmit={async (values, { setSubmitting }) => {

                          this.setState({lockcontract: 'loading'});

                          values.datepicker = this.state.datepicker;

                          const token_ = values.token;
                          const beneficiary_ = values.beneficiary;
                          const releaseTime_ = this.state.datepicker;
                          var releasePrice_ = values.price;

                          var myContract = new this.state.web3.eth.Contract(LockTokenForm.minABI, token_);
                          const accounts = await this.state.web3.eth.getAccounts();
                          let currentAccount = accounts[0];
                          this.state.currentAccount = currentAccount;

                          await myContract.methods.decimals().call({from: accounts[0]}, function(error, decimals){
                            var BN = this.state.web3.utils.BN;
                            releasePrice_ = new BN(releasePrice_).mul(new BN('10').pow(new BN(decimals))).toString();
                            
                          }.bind(this));
                          
                          await this.deployLocker(token_, beneficiary_, releaseTime_, releasePrice_);

                        }}
                      >

                      {({ isSubmitting, validateForm }) => (

                      <Form class="form-card" disabled={isSubmitting} >

                        <div class="row justify-content-between text-left" >
                            <div class="form-group col-12 flex-column d-flex"> 
                            <label class="form-control-label px-3">Target token to lock
                              <span class="text-danger"> *</span> 
                                {this.state.selectedOption.icon} ({this.state.selectedOption.label}) 
                              </label>
                            
                            <CreatableSelect
                              placeholder="Select Option"
                              value={this.state.selectedOption}
                              options={this.state.options}
                              onChange={(e) => {
                                this.handleChange.bind(this)(e);
                                validateForm();
                              }}
                              
                              filterOption={createFilter({
                                matchFrom: 'any',
                                stringify: option => `${option.label}`,
                              })}
                              name="token"

                            />

                            {!this.state.selectedOption.valid && 

                            <div class="alert alert-warning" role="alert">
                              Invalid Token!
                            </div>
                            }

                            {this.state.selectedOption && 
                              <div style={{ marginTop: 20, lineHeight: '25px' }}>
                                <b>token contract:</b> {this.state.selectedOption.value}
 
                              </div>
                            }

                            <ErrorMessage name="token" component="div" class="alert alert-danger alert-dismissible fade show" />
                            </div>
                        </div>
                        <hr />

                          <div class="row justify-content-between text-left">
                              <div class="form-group col-sm-6 flex-column d-flex"> <label class="form-control-label px-3">Unlock Price In USDC<span class="text-danger"> *</span></label>
                              <Field type="number" id="price" name="price" placeholder=""  />
                              <ErrorMessage name="price" component="div" class="alert alert-danger alert-dismissible fade show" />
                              </div>

                              <div class="form-group col-sm-6 flex-column d-flex"> <label class="form-control-label px-3">Unlock Date<span class="text-danger"> *</span></label>
                              <input id="datepicker" name="datepicker" />
                              <ErrorMessage name="datepicker" component="div" class="alert alert-danger alert-dismissible fade show" />
                              </div>
                          </div>

                          <div class="row justify-content-between text-left">
                              <div class="form-group col-12 flex-column d-flex"> <label class="form-control-label px-3">Beneficiary address<span class="text-danger"> *</span></label>
                              <Field type="text" id="beneficiary" name="beneficiary" placeholder="0x..." />
                              <ErrorMessage name="beneficiary" component="div" class="alert alert-danger alert-dismissible fade show" />
                              </div>
                          </div>

                          <div class="row justify-content-end">
                              <div class="form-group col-sm-6"> <button type="submit" id="submit" class="btn-block btn btn-lg btn-primary" disabled={isSubmitting} >Submit</button> </div>
                          </div>
                      </Form>

                      )}
                      </Formik>



                  </div>
              </div>
          </div>
      </div>

    );
  }


  async deployLocker(token_, beneficiary_, releaseTime_, releasePrice_){
    console.log('deployLocker');

    const myContract = new this.state.web3.eth.Contract(abi);
    let netid = await this.state.web3.eth.net.getId();
    const Oracle_Contract = new this.state.web3.eth.Contract(o_abi, o_networks[netid].address);
    const currentAccount = this.state.currentAccount;


    myContract.deploy({
        data: bytecode,
        arguments: [token_, beneficiary_, releaseTime_, releasePrice_]
    })
    .send({
        from: this.state.currentAccount,
        gas: 3000000,
    },function(error, transactionHash){ console.log(error); })
    .on('error', function(error){ console.log(error); })
    .on('receipt', function(receipt){
       console.log(receipt.contractAddress) // contains the new contract address
       
    })
    .on('confirmation', function(confirmationNumber, receipt){ 
      console.log(confirmationNumber); 
    })
    .then(function(newContractInstance){
        console.log(newContractInstance.options.address) // instance with the new contract address
        let lock_add = newContractInstance.options.address;

        this.setState({lockcontract: lock_add});
        
        
        Oracle_Contract.methods.add_lock(lock_add).send({
            from: currentAccount,
            gas: 3000000,
        }).then(function(){

          this.setState({done: true});

          window.setTimeout(function() {
            this.props.history.push('/lock-details/' + lock_add);
            
            }.bind(this), 3000);

        }.bind(this));

    }.bind(this)).catch(function(error){
    });

  }


}

export default withRouter(LockTokenForm);
