import React, { Component } from "react";
import TokenTimelock from "./contracts/TokenTimelock.json";
import Web3Comp from "./components/Web3tools";
import LockTokenForm from "./components/TokenLocks";
import { BrowserRouter, Route, Switch, Link, NavLink, withRouter } from 'react-router-dom';
import Home from './routes/Home';
import Roadmap from './routes/Roadmap';
import Contact from './routes/Contact';
import MyLocks from './routes/MyLocks';
import OLKToken from './routes/OLKToken';
import LockDetails from "./components/LockDetails";
import logo from './images/dl_logo.jpg'


import "./App.css";
import {dataService} from './components/user-context';

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      web3: null,
      search: '',
      redirect: false
    };
  }

  handleSearch(){

    let search = this.state.search;
    let errors = false;

    if (!search) {
      errors = 'Required';
    } else if (
      !/^0x[a-fA-F0-9]{40}$/.test(search)
    ) {
      errors = 'Invalid beneficiary address';
    }

    if(errors){
      alert("Invalid Address");

    }else{
      //window.location = "/OracleLocker/lock-details/" + this.state.search
      this.setState({redirect: true});
      this.props.history.push("/lock-details/" + this.state.search);
    }
  }

  handleKeyPress = (event) => {
    if(event.key === 'Enter'){
      this.handleSearch()
    }
  }


  render(){
     return (
       <div className="component-app">

            <nav class="navbar navbar-expand-lg navbar-dark bg-dark" >
              <NavLink to={'/'} className={({ isActive }) => (isActive ? 'navbar-brand btn active' : 'navbar-brand btn inactive')} >  
              <img src={process.env.PUBLIC_URL + "/favicon.png"} width="30" height="30" class="d-inline-block align-top" alt=""/>
              Oracle Locker
              </NavLink>
              
              <ul className="navbar-nav mr-auto ">
                <li class="nav-item" >
                  <NavLink to={'/mylocks'} className={({ isActive }) => (isActive ? 'nav-link btn active' : 'nav-link btn inactive')} > My Locks </NavLink>
                </li>
                <li class="nav-item" >
                  <NavLink to={'/locktoken'} className={({ isActive }) => (isActive ? 'nav-link btn active' : 'nav-link btn inactive')}>Lock Tokens</NavLink>
                </li>
                <li class="nav-item" >
                  <NavLink to={'/roadmap'} className={({ isActive }) => (isActive ? 'nav-link btn active' : 'nav-link btn inactive')}>Roadmap</NavLink>
                </li>

                <li class="nav-item" >
                  <NavLink to={'/olk'} className={({ isActive }) => (isActive ? 'nav-link btn active' : 'nav-link btn inactive')}>OLK Token</NavLink>
                </li>
              </ul>

              <div class="form-inline" >
                  <input class="form-control mr-sm-2 " type="search" placeholder="0x... address" aria-label="Search" value={this.state.search}
                    onChange={
                      (e)=> this.setState({search: e.target.value})
                    } 
                    onKeyPress={this.handleKeyPress}
                  />
                  <button class="btn btn-outline-success my-2 my-sm-0 btn-sm" type="button" onClick={this.handleSearch.bind(this)} >Search By Lock Contract</button>
              </div>

              <Web3Comp></Web3Comp>

            </nav>


            <div class="modal fade bd-example-modal-sm" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel" id="mySmallModalLabel" aria-hidden="true">
              <div class="modal-dialog modal-sm">
                <div class="modal-content">
                  <div class="row text-center">
                      <div class="col-md-12">
                          <span id="status"></span>
                      </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="container">

              <Switch>

                <Route exact path="/" >
                  <Home web3={this.state.web3} />
                </Route>

                <Route path="/mylocks" >
                  <MyLocks web3={this.state.web3} currentAccount={this.state.currentAccount} />
                </Route>
                
                <Route path="/locktoken" >
                  <LockTokenForm web3={this.state.web3} currentAccount={this.state.currentAccount} />
                </Route>

                <Route path="/roadmap">
                  <Roadmap />
                </Route>

                <Route path="/olk">
                  <OLKToken />
                </Route>

                <Route path="/lock-details/:contract" >
                  <LockDetails web3={this.state.web3} currentAccount={this.state.currentAccount} />
                </Route>
              </Switch>
              
            </div>


          <div class="container">

          <div class="card mb-3" style={{"max-width": "540px;"}}>
          <div class="row g-0">
            <div class="col-md-4">
              <img src={logo} class="img-fluid rounded-start" alt="..." />
            </div>
            <div class="col-md-8">
              <div class="card-body">
                <h5 class="card-title">Powered By <a href="https://github.com/DecentralLabs/" >DecentralLabs</a> Team</h5>
                <p class="card-text">
                  An Innovation Edge In DeCentral Technologies. Unstoppable economics management.
                </p>
              </div>
            </div>
          </div>
          </div>
      
          </div>
      </div>
     );
  }

  componentDidMount() {

    dataService.getData().subscribe(message => {
      
      this.setState({ web3: message.value.web3, currentAccount: message.value.currentAccount});

    });
  }

}

export default withRouter(App);
