import React, { Component } from "react";
import {dataService} from './user-context';
import { withRouter } from 'react-router-dom';

declare var $ : any;

const Web3 = require('web3');


class Web3Comp extends Component {

  static networks = {
    "bsc_testnet": "0x"+(97).toString(16),
    "bsc": "0x"+(56).toString(16)
  };

  static target_net = Web3Comp.networks["bsc"];

componentDidMount() {

  const m = this.detectMetaMask();

  if(m) {
      //this.loadBlockchainData();

      window.addEventListener('load', function(e) {
        this.loadBlockchainData();
    }.bind(this), false);


  } else {
    //alert(m);
      $('#status').html('Metamask Required!');
      $('#mySmallModalLabel').modal('show');
  }

}

async swap_network(network_name){

  Web3Comp.target_net = Web3Comp.networks[network_name];

  let network_name_set = "BSC MainNet";
  if(network_name != "bsc"){
    network_name_set = "BSC TestNet";
  }

  this.setState({network_name: network_name_set});

  await this.NetworkFit();
  this.refresh();

}

async loadBlockchainData() {

    await this.connect();
    this.NetworkFit();

    // detect Metamask account change
    window.ethereum.on('accountsChanged', function (accounts) {
      console.log('accountsChanges',accounts);
      this.handleAccountsChanged(accounts);
    }.bind(this));

    // detect Network account change
   window.ethereum.on('networkChanged', function(networkId){
     console.log('networkChanged',networkId);
     this.NetworkFit();
   }.bind(this));

}

refresh(){
  let location = window.location.pathname.replace("/OracleLocker", "")
  this.props.history.push("/");
  setTimeout(() => this.props.history.push(location), 1);
}


constructor(props) {
  super(props)
  this.state = {
    currentAccount: '',
    web3: null,
    abi: '',
    bytecode: '',
    network_name: 'BSC MainNet',
    redirect: false
  }

}

render() {

  return (
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">

        </ul>

        <button class="btn btn-primary" onClick={this.refresh.bind(this)} >Refresh</button>

        <div class="btn-group">
          <button type="button" class="btn btn-success dropdown-toggle" data-toggle="dropdown" aria-expanded="false">
            Network ({this.state.network_name})
          </button>
          <div class="dropdown-menu">
            <a className={(this.state.network_name=='BSC MainNet') ? "dropdown-item active" : "dropdown-item"}
              onClick={this.swap_network.bind(this, "bsc")} >
              <i class="icon-play" ></i> BSC MainNet
            </a>
            <a className={(this.state.network_name=='BSC MainNet') ? "dropdown-item" : "dropdown-item active"}
            onClick={this.swap_network.bind(this, "bsc_testnet")} ><i class="icon-play" ></i> BSC TestNet</a>
          </div>
        </div>

        <ul class="navbar-nav mr-2">

        </ul>

        <div class="btn-group">
          <button type="button" class="btn btn-success my-2 my-sm-0" id="connect_bt" onClick={this.connect.bind(this)} > <i class="icon-play" ></i> Connect</button>
        </div>

    </div>
  );
}

async NetworkFit(){

  var bsc_testnet = Web3Comp.networks["bsc_testnet"];
  var bsc_mainnet = Web3Comp.networks["bsc"];
  var target_net =  Web3Comp.target_net; //bsc_mainnet;

  if(this.state.web3 == null){
    return;
  }

  var netid = await this.state.web3.eth.net.getId();

  if(netid == target_net){
    return true;
  }

  try {
        await window.ethereum.request({
              method: 'wallet_switchEthereumChain',
              params: [{ chainId: target_net }],
            });
  } catch (switchError) {
    // This error code indicates that the chain has not been added to MetaMask.
    if (switchError.code == 4902 || switchError.code == -32603) {
      try {

        let params;

        if(target_net == bsc_testnet){

          params = [{
            chainId: target_net,
            chainName: 'BSC TestNet',
            nativeCurrency: {
              name: 'BNB',
              symbol: 'BNB',
              decimals: 18
            },
            rpcUrls: ['https://data-seed-prebsc-1-s1.binance.org:8545/'],
            blockExplorerUrls: ['https://testnet.bscscan.com']
          }];

        }else{
          //BSC mainet
          params = [{
            chainId: target_net,
            chainName: 'BSC MainNet',
            nativeCurrency: {
              name: 'BNB',
              symbol: 'BNB',
              decimals: 18
            },
            rpcUrls: ['https://bsc-dataseed.binance.org/'],
            blockExplorerUrls: ['https://bscscan.com']
          }];

        }

        

        await window.ethereum.request({
          method: 'wallet_addEthereumChain',
          params: params,
        });
      } catch (addError) {
        // handle "add" error
        console.log(addError);
      }
    }
    // handle other "switch" errors
  }
}

 async handleAccountsChanged(accounts) {
    console.log('Calling HandleChanged')

    if (accounts.length === 0) {
        console.log('Please connect to MetaMask.');
        $('#status').html('Connect with Metamask');
        $('#connect_bt').html("<i class='icon-play' ></i> Connect");
        $('#mySmallModalLabel').modal('show');
    } else if (accounts[0] !== this.state.currentAccount) {
        this.state.currentAccount = accounts[0];
        $('#connect_bt').html("<i class='icon-play' ></i> Connect");
        $('#status').html('')

        if(this.state.currentAccount != null) {
            // Set the button label
            let label = this.state.currentAccount;
            label = label.substring(0, 6) + "..." + label.substring(label.length-4, label.length);
            $('#connect_bt').html("<i class='icon-play' ></i> Connected To: <b>" + label + "</b>");
            console.log(label);

        }
    }
    console.log('WalletAddress in HandleAccountChanged ='+this.state.currentAccount);
    dataService.setData({ web3: this.state.web3, currentAccount: this.state.currentAccount});
}

async  connect() {

    let web3 = null;
   // Modern dapp browsers...
   if (window.ethereum) {
     try {
       // Request account access if needed
       const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
       web3 = new Web3(window.ethereum);
       // Accounts now exposed
     } catch (error) {
       console.log(error);
           // EIP-1193 userRejectedRequest error
           // If this happens, the user rejected the connection request.
           console.log('Please connect to MetaMask.');
           $('#status').html('You refused to connect Metamask!');
           $('#mySmallModalLabel').modal('show');
           $('#connect_bt').html("<i class='icon-play' ></i> Connect");
           return;
       
     }
   }
   // Legacy dapp browsers...
   else if (window.web3) {
     // Use Mist/MetaMask's provider.
     web3 = window.web3;
     console.log("Injected web3 detected.");
   }
   // Fallback to localhost; use dev console port by default...
   else {
     const provider = new Web3.providers.HttpProvider(
       "http://127.0.0.1:8545"
     );
     web3 = new Web3(provider);
     console.log("No web3 instance injected, using Local web3.");
   }

   const accounts = await web3.eth.getAccounts();
   this.setState({ web3: web3});

   await this.NetworkFit();
   await this.handleAccountsChanged(accounts);
   await this.NetworkFit();
   await dataService.setData({ web3: web3, currentAccount: this.state.currentAccount});

}

 detectMetaMask() {
    if (typeof window.ethereum !== 'undefined') {
        return true
    } else {
        return false
    }
}



}

export default withRouter(Web3Comp);
