import React, { Component } from 'react';

import {dataService} from '../components/user-context';
import {abi, bytecode} from '../contracts/TokenTimelock.json'
import {abi as o_abi, networks as o_networks} from '../contracts/OracleLocker.json'
import LockItem from '../components/LockItem';


class MyLocks extends Component {

  static web3;
  static currentAccount;
  static locks = [];

  constructor(props) {
    super(props);

    this.state = {
      web3: props.web3,
      currentAccount: props.currentAccount,
      locks: MyLocks.locks,
      loading: true
    };

    dataService.getData().subscribe(message => {
      
        this.setState({ web3: message.value.web3, currentAccount: message.value.currentAccount, loading: true});
        this.load_locks();
  
    });

    if(this.state.web3){
        this.load_locks();
    }
    

  }


  render() {

    if(this.state.loading){
      return <div class="card border-info mb-3" >
      <div class="spinner-border" role="status">
      </div>
      </div>;
    }

    return (
        <div class="container">

        <div class="card">
            <div class="card-body">
                <h5>My Locks</h5>
                
                <p>
                    Here will find all locks generated by your wallet. 
                    <div class="alert alert-success" role="alert">
                        {this.state.currentAccount}
                    </div>
                </p>

                {this.state.locks.length == 0 && 
                    <div class="alert alert-warning" role="alert">
                        Any previous locks detected from your wallet!
                    </div>
                }
            </div>
        </div>

        {this.state.locks.length > 0 && 
            this.state.locks.map(function (element, i) {
                    return <LockItem contract={element} web3={this.state.web3} />
                }.bind(this)
            )
        }


            
              
          
        </div>
    );
  }

  async  load_locks() {

    const accounts = await this.state.web3.eth.getAccounts();
    MyLocks.currentAccount = accounts[0];
    this.setState({currentAccount: accounts[0]});

    var netid = await this.state.web3.eth.net.getId();

    var myContract = new this.state.web3.eth.Contract(o_abi, o_networks[netid].address);
    
    await myContract.methods.get_locks(this.state.currentAccount).call({from: this.state.currentAccount}, function(error, result){
      //alert(result);

      this.setState({ locks: result, loading: false });
      MyLocks.locks = result;
      
    }.bind(this));

  }

}

export default MyLocks;